.App {
    text-align: center;
    font-family: 'Heebo';
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.body {

    padding: 25px 50px 25px 50px;
}

@media (max-width: 767px) {
    .hidden-mobile {
        display: none;
    }

    .body {

        padding: 5px 5px 5px 5px;
    }
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.toolbar {
    height: 56px;
}


.link {
    color: #607AFF;
}

.nav {
    display: inline-flex;
    position: relative;
    overflow: hidden;
    max-width: 100%;
    padding: 0 20px;
}

.MuiDataGrid-root .MuiDataGrid-cell:focus-within {
    outline: none !important;

}

.MuiDataGrid-cell {
    text-overflow: ellipsis !important;
}

.nav-item {
    color: #b2b1b9;
    padding: 20px;
    text-decoration: none;
    transition: 0.3s;
    margin: 0 6px;
    z-index: 1;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 500;
    position: relative;
}

.nav-item:before {
    content: "";
    position: absolute;
    bottom: -6px;
    left: 0;
    width: 100%;
    height: 4px;
    background-color: #dfe2ea;
    border-radius: 4px 4px 0 0;
    opacity: 0;
    transition: 0.3s;
}

svg.MuiDataGrid-iconSeparator {

}

.nav-item:not(.is-active):hover:before {
    opacity: 1;
    bottom: 0;
}

.nav-item:not(.is-active):hover {
    color: #607AFF;
}

.nav-indicator {
    position: absolute;
    left: 0;
    bottom: 0;
    height: 4px;
    transition: 0.4s;
    z-index: 1;
    border-radius: 4px 4px 0 0;
}

@media (max-width: 580px) {
    .nav {
        overflow: auto;
    }
}

div.MuiDataGrid-columnHeader, div.MuiDataGrid-cell {
    padding-left: 20px !important;
}

.findingModal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 50%;
    min-height: 50%;
    background-color: #FFFFFF;
    border: 2px solid #E0E1E8;
    border-radius: 8px;
    padding: 4px;
    padding: 10px 20px 10px 20px;
}

.modalTitle {
    height: 29px;
    font-family: 'Heebo';
    font-style: normal;
    font-weight: 500;
    font-size: 20vm;
    line-height: 29px;
    align-items: flex-end;
    color: #26026C;
    grid-column: span 23;

}

.separator {
    background-color: #E0E1E8;
    height: 2px;
    margin: 10px 0px 10px 0px;
}

.grid-container {
    display: grid;
    grid-template-columns: repeat(24, 1fr);
    margin: 5px 0px 15px 0px;
}

.grid-half {
    grid-column: span 12;
}

.grid-3 {
    grid-column: span 12;
}

.grid-full {
    grid-column: span 24;
    font-family: 'Heebo';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    text-transform: capitalize;
    color: #41425A;
    margin: 20px 0px 0px 0px;
}

.radioGroup {
    padding: 10px 0px 40px 0px;
}

.cell1 {
    font-family: 'Heebo';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    text-transform: capitalize;
    color: #41425A;
    margin-right: 10px;
}

.cell2 {
    font-family: 'Heebo';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    text-transform: capitalize;
    color: #41425A;
    margin-left: 10px;
}

.buttons {
    border-radius: 56px;
    width: 94px;
    height: 28px;
    font-family: 'Heebo';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    margin: 25px 0px 15px 20px;
}

.contained {
    background: #607AFF;
    color: #FFFFFF;
}

.outlined {
    color: #41425A;
    border: 1px solid rgba(65, 66, 90, 1);
}

.buttons:disabled {
    opacity: 0.5;
}

.buttonGroup {
    position: fixed;
    right: 20px;
    bottom: 0;

}

.hideScrollBar {
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none;
    overflow-y: hidden; /* Hide vertical scrollbar */
}

p.alert {
    color: #bf1650;
    font-size: 10px;
}

p.alert::before {
    display: inline;
    content: "âš  ";
}